	module.exports = {"data":{"nodePost":{"title":"The Design Tool Dilemma - freeCodeCamp","link":"https://medium.freecodecamp.org/the-design-tool-dilemma-225541c4ad1d","body":{"value":"<p>Found this article especially relevant as I'm currently following just behind our UX team prototyping in code as they design early mockups using InVision. The spirit of what we're doing is aiming for the \"collaborative\" narrative outlined in this post, but the fact that there is still a handoff occasionally makes it feel like what the article calls “bridging the gap.” It is very early days, but I'm anxious for a future where more of our tools can speak the same language and actually impact code that will be used in the final project. Still a long way to go on that front though.</p>\r\n<p>I found this embedded quote to be especially insightful:</p>\r\n<blockquote>\r\nProduction code is a surrogate for decision-making power. Production code is the source of truth. It is the realtime sum total of all the conversations, all the decisions, all the politics…it is everything. Whoever is pushing code to production is running the product. Everyone else only has influence.\r\n<cite><a href=\"http://www.artypapers.com/ap.log/thread.php?346\">Rebekah Cox</a></cite>\r\n </blockquote>\r\n<p>I don't necessarily want all of that power. In a perfect world, I help enable others to assert control on the areas of the project where they are experts. With our current tools there is just too much risk of things being lost in translation. Today we're doing the best we can with the tools we have and against all odds we often find a way to make it work.</p>"},"relationships":{"image":null},"created":1532035775}},"pathContext":{"slug":"/link/the-design-tool-dilemma-freecodecamp/"}}

